@import-normalize; /* bring in normalize.css styles */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Radio Canada',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  background: #1E1D1D;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}

/*  WebKit (Chrome, Safari) */
*::-webkit-scrollbar {
  width: 6px; /*    */
}

*::-webkit-scrollbar-thumb {
  background-color: transparent; /*    */
  border-radius: 16px; /*   */
}

/*      h1-h6 */
h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
  padding: 0;
}

/*     */
ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

/*      p */
p {
  margin: 0;
  padding: 0;
}

/*      a */
a {
  text-decoration: none;
  color: inherit;
}

/*     body */
body {
  margin: 0;
  padding: 0;
}

/*     button */
button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  font-family: inherit;
}

img {
  max-width: 100%;
}
